.skill{
    background-color: rgba(0, 0, 0, 0.55);
}

.skill svg{
    font-size: 2rem;
}

.skill .card.card__reveal{
    opacity: 0;
    translate: 0px 50px;
    animation: skill-reveal .5s ease-in forwards;
}


.skill .gallery .card {
    max-width: 35%;
}

.skill .gallery .card:hover {
    scale: 1;
}

.on_hover:hover{
    letter-spacing: unset;
}

.skill .card:nth-child(1){
    animation-delay: .5s;
}
.skill .card:nth-child(2){
    animation-delay: 1s;
}
.skill .card:nth-child(3){
    animation-delay: 1.5s;
}
.skill .card:nth-child(4){
    animation-delay: 2s;
}
.skill .card:nth-child(5){
    animation-delay: 2.5s;
}
.skill .card:nth-child(6){
    animation-delay: 3s;
}
.skill .card:nth-child(7){
    animation-delay: 3.5s;
}
.skill .card:nth-child(8){
    animation-delay: 4s;
}
.skill .card:nth-child(9){
    animation-delay: 4.5s;
}
.skill .card:nth-child(10){
    animation-delay: 5s;
}
.skill .card:nth-child(11){
    animation-delay: 5.5s;
}
.skill .card:nth-child(12){
    animation-delay: 6s;
}

.skill svg.html{
    color: #ff8000;
}
.skill svg.css{
    color: #008cff;
}
.skill svg.javascript{
    color: #ffff00
}
.skill svg.reactjs{
    color: #00bbff
}
.skill svg.nodejs{
    color: #04ff00
}
.skill svg.expressjs{
    color: #cecece
}
.skill svg.mysql{
    color: #ddff00
}
.skill svg.mongodb{
    color: #6aff00
}
.skill svg.bootstrap{
    color: #dd00ff
}
.skill svg.mobx{
    color: #f1f1f1
}
.skill svg.redux{
    color: #8c00ff
}
.skill svg.socket{
    color: #ffffff
}

@keyframes skill-reveal {
    from{
        opacity: 0;
        translate: 0px 50px;
    }
    to{
        opacity: 1;
        translate: 0px 0px;
    }
}