.navigation {
    position: relative;
    left: 0;
    right: 0;
    z-index: 1;
    display: flex;
    justify-content: center;
    transition: .5s;
}

.navigation.navigation__fixed{
    position: fixed;
    top: 0;
}

.navigation li.menu {
    display: none;
}


.navigation ul.navigation-links {
    background: rgb(125, 125, 125);
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    animation: nav-init .5s forwards ease-in-out, nav-fill 2s forwards .75s ease-in-out;
}

.navigation ul.navigation-links li {
    opacity: 0;
    cursor: pointer;
    padding: 1rem;
    border-radius: .25rem;
    animation: nav-show 1s forwards 2s ease-in-out;
    transition: .25s;
}

.navigation ul.navigation-links li a {
    color: #FFF;
    text-decoration: none;
}

@keyframes nav-init {
    0% {
        translate: 0 -100%;
    }

    50% {
        translate: 0 50px;
    }

    100% {
        translate: 0 10px;
    }
}

@keyframes nav-fill {
    0% {
        width: 20px;
        height: 20px;
        padding: 0;
    }

    75% {
        height: 20px;
        width: 100%;
        translate: 0 0;
        background: rgba(0, 0, 0);
        padding: 1rem 0;
    }

    90% {
        height: 80%;
        width: 100%;
        border-radius: 0;
        translate: 0 0;
        background: rgba(0, 0, 0);
    }
    
    100%{
        height: 100%;
        width: 100%;
        border-radius: 0;
        translate: 0 0;
        background: rgba(0, 0, 0);
        padding: 1rem 0;
    }
}

@keyframes nav-show {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}