.experience .gallery{
    gap: 20px;
    justify-content: space-around;
}
.experience .card{
    border:0;
}

.experience .card:hover{
    scale: 1;
}

.experience .card.active{
    background:rgba(0, 0, 0, 0.45)
}

.experience .card .card-header img{
    width: 80%;
    object-fit: contain;
}

.experience .card .card-body a{
    font-size: 1.25rem;
}

.experience .card .card-body .experience-detail{
    border-top: 1px solid rgba(255, 255, 255, 0.25);
    margin-top: 1rem;
    padding: .5rem 1.5rem;
}

.experience .card .card-body .experience-detail p{
    margin: .25rem 0;
}