.section.profile{
    margin-top: 2rem;
}

.profile h1{
    font-size: 1.1rem;
    text-decoration: none;
    margin-bottom: 1rem;
}

.profile a.cv{
    margin-top: 1rem;
    text-align: center;
    margin-bottom: 2rem;
    display: inline-block;
    width: 100%;
    color: #FFF;
    font-weight: bold;
    cursor: pointer;
}

.profile-wrapper .profile-detail{
    position: relative;
    text-align: center;
    border-bottom: 1px solid #4f4f4f;
    display: grid;
    grid-template-rows: 2fr 1fr;
}

.profile-wrapper .profile-detail .overlay{
    background: url('https://c8.alamy.com/comp/PF3PC9/desktop-source-code-and-technology-background-developer-or-programer-with-coding-and-programming-wallpaper-by-computer-language-and-source-code-com-PF3PC9.jpg');
    background-size: cover;
    background-position: center;
    width: 100%;
    height: 100%;
    position: absolute;
    z-index:-1;
}

.profile-wrapper .profile-detail .profile-picture-wrapper{
    padding-top: 2rem;
    position: relative;
}

.profile-wrapper .profile-detail img{
    border-radius: 50%;
    object-fit: cover;
    max-width: 200px;
}

.profile-wrapper .profile-detail .social-media_links{
    margin: 2rem 0;
    display: flex;
    justify-content: center;
    gap: 25px;
    align-items: center;
    padding: .5rem;
}

.profile-wrapper .profile-detail ul.social-media_links li{
    cursor: pointer;
    transition: .25s;
}

.profile-wrapper .profile-detail ul.social-media_links li:hover svg{
    scale: 1;
    translate: 0 0;
    transition: .25s ease-in-out;
}

.profile-wrapper .profile-detail ul.social-media_links li svg{
    scale: 0;
    translate: 100px 0;
}

.profile-wrapper .profile-detail ul.social-media_links li span{
    margin-left: .25rem;
    transform: translateY(5px);
}

.profile-wrapper .profile-description{
    text-align: justify;
    font-size: 1rem;
    margin-top: 3rem;
}

.profile-wrapper .profile-academic{
    margin-left: 0;
    margin-right: 0;
    margin-top: 4rem;
}

.profile-wrapper .profile-academic h2{
    font-size: 1.2rem;
    margin-bottom: 1rem;
    text-decoration: none;
    font-weight: bold;
    translate: -200px 0;
    opacity: 0;
    animation: slideTitleList 1s forwards ease-in;
    animation-delay: 1s;
}

.profile-wrapper .profile-academic h2 svg{
    translate: -1px -2px;
}

