.profile-wrapper .profile-academic ul li{
    margin: 1rem 0;
    list-style: disc;
    translate: -200px 0;
    opacity: 0;
    animation: slideList 1s forwards ease-in;
    animation-delay: 2s;
}

@keyframes slideTitleList {
    from {
        translate: -200px 0;
        opacity: 0;
    }
    to {
        translate: 0 0;
        opacity: 1;
        /* background-color: yellow; */
    }
  }

@keyframes slideList {
    from {
        translate: -200px 0;
        opacity: 0;
    }
    to {
        translate: 0 0;
        opacity: 1;
    }
  }